.App {
  text-align: center;
}

.list {
  margin-left: 300px;
  margin-top: 75px;
}
@media (max-width: 600px) { 
  .list {
    margin-left: 0px;
    margin-top: 75px;
  }
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
